
#favorslider .noUi-tooltip {
    display: none;
}
#favorslider .noUi-active .noUi-tooltip {
    display: block;
}

#favorslider .noUi-active {
    border-color: pink;

}
#favorslider .noUi-tooltip {
     border-color: pink;
 }

#favorslider :focus {
    outline-style: none;
    outline-color: pink;
}

#favorslider .noUi-connect {
    background: rgb(240, 98, 146);
}
#favorslider .noUi-connects {
    background: rgba(240, 98, 146,0.05);;
    border-radius: 0;
}

#favorslider.noUi-target {
    height: 5px ;
    border-radius: 1px;
    border: 1px solid rgba(240, 98, 146,0.25);;
}
#favorslider .noUi-handle {
    opacity: 0;
    height: 16px;
    top: -6.5px;
    width: 16px;
    right: -8px;
}

#favorslider .noUi-handle:hover, #favorslider .noUi-handle:active {
    opacity: 1;
}

#favorslider .noUi-handle:before, #favorslider .noUi-handle:after{
    height: 80%;
    top: 10%;
}

#favorslider .noUi-handle:before {
    left: 5px;
    display: none;
}

#favorslider .noUi-handle:after {
    right: 5px;
    left: auto;
    display: none;
}

#favorslider .noUi-tooltip{
    border-radius: 5px;
}

