

#levelslider .noUi-tooltip {
    display: none;
}
#levelslider .noUi-active .noUi-tooltip {
    display: block;
}

#levelslider .noUi-active {
    border-color: rgba(248,210,6,0.35);

}
#levelslider .noUi-tooltip {
    border-color: rgba(248,210,6,0.35);
}

#levelslider :focus {
    outline-style: none;
    outline-color: rgba(248,210,6,0.35);
}

#levelslider .noUi-connect {
    background: rgb(248,210,6);
}
#levelslider .noUi-connects {
    background: rgba(248,210,6,0.05);;
    border-radius: 0;
}

#levelslider.noUi-target {
    height: 5px ;
    border-radius: 1px;
    border: 1px solid rgba(248,210,6,0.25);;
}
#levelslider .noUi-handle {
    opacity: 0;
    height: 16px;
    top: -6.5px;
    width: 16px;
    right: -8px;
}

#levelslider .noUi-handle:hover, #levelslider .noUi-handle:active {
    opacity: 1;
}

#levelslider .noUi-handle:before, #levelslider .noUi-handle:after{
    height: 80%;
    top: 10%;
}

#levelslider .noUi-handle:before {
    left: 5px;
    display: none;
}

#levelslider .noUi-handle:after {
    right: 5px;
    left: auto;
    display: none;
}

#levelslider .noUi-tooltip{
    border-radius: 5px;
}

